<script>
import html2canvas from "html2canvas"
import Vue from "vue";
import Qs from "qs";
const axios = require("axios");
import pitpatSound from "@/assets/mp3/1.mp3";
export default {
  data() {
    return {
      ROLEID:'',
      SessionKey: "",
      examineData:'',
      loading: false,
      imgURL:'',
      examineDatas: {
        DeviceID: "tj001",
        BusinessType: "",
        BusinessNo: "123456789",
        CarType: "C1",
        IDCardName: "",
        IDCardSex: "",
        CardType: 0,
        IDCardNo: "140424199707073639",
        PhoneNo: "18835567639",
        IDCardAddress: "",
        UnAudit: 19,
        Audited: 11,
        Adopt: 2,
        Fail: 3,
        QGIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathJb.jpg",
        SLZIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathZy.jpg",
        SLYIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathYy.jpg",
        BSLIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathBsl.jpg",
        TLIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathTl.jpg",
        SGIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathBsl.jpg",
        XZIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathTbzp.jpg",
        SZZIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathSz.jpg",
        SZYIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathSz.jpg",
        IDCardIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathSfzmzp.jpg",
        ExamineIMG:
          "http://tm-pictures.obs.cn-north-1.myhuaweicloud.com/PhotoResources/2019/10/11/tjjsbbh999test/1910110007192/PathSfzmzp.jpg",
        QG: '0',
        BSL: '0',
        TL: '0',
        SZZ: '0',
        SZY: '0',
        XZZ: '0',
        XZY: '0',
        SL: 5.0,
        ZYJZ: '0',
        YYJZ: '0',
        ZTQ: '0',
        SG: 170,
        VideoSrc: "",
        VideoDownload: ""
      },
      timer: null,
      pitpatSound,
      idcard1:"",
      idcard2:"",
      dialogVisible: false,
      dialogVisible1: false,
      dialogVisible2: false,
      dialogVisible3: false,
      dialogVisible4: false,
      dialogVisible5: false,
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: false, //true,
        muted: false, // 
        loop: false, // 
        // preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "",
            src: "" //url
          }
        ],
        poster: "", //
        // width: document.documentElement.clientWidth,
        notSupportedMessage: "", //Video.js
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: true,
          fullscreenToggle: true //
        }
      },
      reason: "", //
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      Status:''
    };
  },
  methods: {
    //
    getData(){
      this.loading = true
      axios({
        method: "post",
        url: http + "api/ExamineWeb/GetExamine",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: Qs.stringify({
          SessionKey: this.SessionKey
        })
      })
      .then(res => {
        this.loading = false
        console.log(res);
        if (res.data.IsSuccess) {
          this.examineData = res.data.Data
          if(!res.data.Data.BusinessNo){
            // this.examineData = this.examineDatas
            this.setTimer()
          }
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      });
    },
    //
    auditData(){
      axios({
        method: "post",
        url: http + "api/ExamineWeb/Audit",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: Qs.stringify({
          BusinessNo: this.examineData.BusinessNo,
          Status:this.Status,
          SessionKey:this.SessionKey,
          AuditExplain:this.reason,
          TableBase64:this.imgURL
        })
      })
      .then(res => {
        this.loading = false
        console.log(res);
        if (res.data.IsSuccess) {
          this.$message({
            type: "success",
            message: res.data.Message
          });
          this.getData()
        } else {
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      });
    },
    //
    videoData(){
      this.loading = true
      axios({
        method: "post",
        url: http + "api/ExamineWeb/GetVideoUrl",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: Qs.stringify({
          BusinessNo: this.examineData.BusinessNo,
          DeviceID:this.examineData.DeviceID,
          SessionKey:this.SessionKey
        })
      })
      .then(res => {
        this.loading = false
        console.log(res);
        if (res.data.IsSuccess) {
          this.playerOptions.sources[0].src = 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4'
          this.$message({
            type: "success",
            message: res.data.Message
          });
        } else {
          this.playerOptions.sources[0].src = 'http://clips.vorwaerts-gmbh.de/big_buck_bunny.mp4'
          this.$message({
            showClose: true,
            message: res.data.Message,
            type: "error"
          });
        }
      })
      .catch(error => {
        this.loading = false
        this.$message({
          showClose: true,
          message: "",
          type: "error"
        });
      });
    },
    openVisible() {
      this.dialogVisible = true;
    },
    openVisible2() {
      this.dialogVisible2 = true;
    },
    openVisible3() {
      this.dialogVisible3 = true;
      
    },
    //
    openVisible4() {
      if (this.reason == "") {
        this.$message({
          type: "info",
          message: ""
        });
      } else {
        this.loading = true
        this.Status = -1
        this.auditData()
        this.dialogVisible4 = false;
        this.reason = "";
      }
    },
    openVisible6() {
      this.dialogVisible4 = false;
      this.reason = "";
    },
    //
    openVisible5(){
      this.videoData()
      setTimeout(()=>{
        this.dialogVisible5 = true
      },100)
      
    },
    //
    openVisible7() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.loading = true
          this.Status = 1
          //DOM
          let that = this
          let imgHeight = window.document.querySelector("#code").offsetHeight; // DOM
          let imgWidth = window.document.querySelector("#code").offsetWidth; // DOM
          let scale = window.devicePixelRatio; // 
          html2canvas(that.$refs.imageWrapper, {
            backgroundColor: null, //
            useCORS: true, //
            scale: scale, //2
            width: imgWidth,
            height: imgHeight,
            imageTimeout: 5000, //0
            proxy: "", //url
            ignoreElements: element => {
              //true
              if (element.id == "mytitle") {
                return true;
              }
            }
          }).then(canvas => {
            let imgURL1 = canvas.toDataURL("image/png");
            console.log(imgURL1)
            that.imgURL = imgURL1.substr(22);
            that.auditData()
          })
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    setTimer() {
      this.timer = setInterval(() => {
        this.loading = true
        axios({
          method: "post",
          url: http + "api/ExamineWeb/GetExamine",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          },
          data: Qs.stringify({
            SessionKey: this.SessionKey
          })
        })
        .then(res => {
          this.loading = false
          console.log(res);
          if (res.data.IsSuccess) {
            this.examineData = res.data.Data
            if(res.data.Data.BusinessNo){
              this.clearTimer()
            }
          } else {
            this.$message({
              showClose: true,
              message: res.data.Message,
              type: "error"
            });
          }
        })
        .catch(error => {
          this.loading = false
          this.$message({
            showClose: true,
            message: "",
            type: "error"
          });
        });
      }, 10000);
    },
    //
    clearTimer() {
      clearInterval(this.timer)
    },
    //
    videoDowload() {
      this.videoData()
      setTimeout(()=>{
        window.open(this.playerOptions.sources[0].src);
      },100)
    }
  },
  mounted() {
    Vue.buttonAudio = document.getElementById("music");
    Vue.buttonAudio.pause();
    this.SessionKey = sessionStorage.getItem("SessionKey");
    this.ROLEID = sessionStorage.getItem("ROLEID")
    this.getData()
  },
  beforeDestroy() {
    this.clearTimer()
  },
  watch: {
    "examineData.BusinessNo": {
      handler: (val, olVal) => {
        if (!olVal&&val) {
          Vue.buttonAudio.play();
        }

      }
    },
    dialogVisible5: function(newVal) {
      if (newVal == false) {
        this.$refs.videoPlayer.player.pause();
      }
    }
  }
};
</script>
